<script>
    import Game from "./Game.svelte"
    import Instructions from "./Instructions/Instructions.svelte"
    import Redirect from './Redirect.svelte'
    import Password from "./Password.svelte"
    import BlockEnd from "./BlockEnd.svelte"
    let instructionsDone=false
    let gameEnd=false
    let passedKey=false
    let writeKey=undefined
    let id = undefined
    let gameData=""
    let years = 6
    let yearEnd=false
    let greenArray = []
    let yearCounter = 1
    function toGame(){
        instructionsDone = true
    }
    function toNext(data,greens){
        if (yearCounter < years){
            yearEnd=true
            gameData=data
            greenArray.push(greens)
            yearCounter+=1
        }
       else{
           gameEnd=true
       }
    }
    function getData(data){
        gameData=data
        console.log(data)
    }
    function getWriteKey(writeKey_d,id_d){
        passedKey=true
        id =id_d
        writeKey = writeKey_d
    }
    function nextYear(){
        yearEnd=false
    }
</script>
{#if !passedKey}
    <Password writeKeyPass={getWriteKey}/>
{/if}
{#if passedKey && instructionsDone===false}
    <Instructions toGame={toGame} getData={getData} writeKey={writeKey} id={id}/>
{/if}
{#if (instructionsDone && !gameEnd)}
    {#if yearEnd}
        <BlockEnd greenScore={greenArray[greenArray.length-1]} greenScoreLast={(greenArray.length>1)?greenArray[greenArray.length-2]:null} firstBlock={(greenArray.length>1)?false:true} nextYear={nextYear} lastBlock={greenArray.length === years}/>
    {:else}
        <Game toNext={toNext} gameString={gameData} writeKey={writeKey} id={id} totalBlocks={years} block={yearCounter}/>
    {/if}
    <!-- <Game toDebrief={toDebrief} gameString={gameData} writeKey={writeKey} id={id} totalBlocks={6} block={1}/>
    -->
{/if}
{#if gameEnd}
    <Redirect/>
{/if}

